export default {
	data() {
		return {
			disabled: true,
			phoneNum: '',
			code: ''
		};
	},
	methods: {
		getCode() {
			if (this.phoneNum === '') {
				uni.showToast({
					title: '请先输入手机号',
					duration: 1000,
					icon: 'none'
				});
			}
		},
		getPhoneNum(e) {
			if (e.detail.value) {
				this.phoneNum = e.detail.value
			}
		},
		getCodeInfo(e) {
			if (e.detail.value) {
				this.code = e.detail.value
				if (this.phoneNum !== '') {
					this.disabled = false
				} else {
					this.disabled = true
				}
			}else{
				this.disabled = true
			}
		},
		complete() {
			console.log('点击了完成')
			uni.showToast({
				title: '验证码错误',
				duration: 1000,
				icon: 'none'
			})
		}
	}
}
